*{
  margin: 0;
  padding: 0;
}

.btn{
  position: absolute;
  top: 33%;
  left: 50%;
  transform: translate(-50%, -50%);
}
h1{
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.container{
  position: absolute;
  top: 60%;
  left: 50%;
  transform: translate(-50%, -50%);
  height: 30%;
  width: 50%;
  border: 2px solid black;
  border-radius: 50px;
}